.App {
    height: 100vh;
    background: url("./assets/images/cold.jpg");
    background-position: center;
    transition: 0.4s ease;
}
.App.warm{ 
    background: url("./assets/images/bg.jpg"); 
    background-position: center;
}
.cover {
    background-image: linear-gradient(to bottom, rgb(0 0 0 / 19%),rgb(0 0 1 / 88%));
    height: 100vh;
    padding:25px;
}
.search-box {
    position: relative;
    margin-top:70px;
} 
.searchInput {
    border: 0;
    background: #f2f2f2b8;
    padding: 8px 5px;
    width: 97%;
    border-radius: 5px;
    /* position: absolute; */
    top: -12px;
    box-shadow: 3px 5px 5px 0px rgb(0 0 0 / 20%);
}
.location-box {
    margin-top: 80px;
}
.location {
    color: #fff;
    font-size: 26px;
    font-weight: 800;
    text-align: center;
} 
.date {
    text-align: center;
    font-size: 16px;
    margin-top: 0px;
    color: #fff;
} 
.weatherBox {
    margin-top: 30px;
    text-align: center;
} 
.temp {
    font-size: 49px;
    padding: 20px;
    background: #ffffff8c;
    color: #fff;
    font-weight: 900;
    text-shadow: 2px 2px #00000066;
    font-family: fantasy;
    border-radius: 10px;
} 
.weather {
    margin-top: 20px;
    color: #fff;
    font-size: 36px;
    font-weight: 800;
}